.searchbox {
  width: 100%;
  box-sizing: border-box;
}

.searchbox__spacer {
  flex-grow: 1;
}

.searchbox__wrapper {
  display: flex;
  flex-wrap: nowrap;
  position: relative;
}

.searchbox__box {
  position: relative;
  box-sizing: border-box;
}

.searchbox__box-content {
  position: relative;
  background-color: #fff;
  height: 100%;
  padding: 10px;
  cursor: pointer;
  perspective: 500px;

  border-right: 1px solid #eee;
}

.searchbox__popup {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #fff;
  transform-origin: 0 50%;
  padding: 10px;

  transform: translateY(10px) rotateX(-10deg);
  transform-origin: 50% 0;
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease;

  &.searchbox__popup--active {
    visibility: visible;
    opacity: 1;
    transform: translateY(10px) rotateX(0);
  }
}

/* Horizontal layout */
.searchbox--horizontal {
  // padding: 13px;

  .searchbox__wrapper {
    flex-direction: row;
  }
}

.searchbox--vertical {
  .searchbox__wrapper {
    flex-direction: column;
  }
}


.searchbox__wrapper-4 {

  .searchbox__box--button {
    width: 20%;
  }
  
  .searchbox__box--occupancy {
    width: 30%;
  }

  .searchbox__box--datepicker {
    width: 55%;
  }
}

.searchbox__box--datepicker {
  .searchbox__box-content {
    width: 50%;
    float: left;

    &:last-child {
      margin-right: 0;
    }
  }
}

.searchbox__box--checkin,
.searchbox__box--checkout {
  .hotel-input {
    display: block;
    font-weight: bold;
    border: 0;

    &:focus {
      outline: none;
    }
  }
}

.searchbox__submit {
  width: 100%;
  height: 100%;
  border: 0;
  cursor: pointer;
}

.searchbox-spinner {
  display: block;
  position: relative;
  padding: 0 40px;
  text-align: center;
  height: 40px;
  line-height: 40px;
}

.searchbox-spinner__output {
  font-weight: bold;
}

.searchbox-spinner__input {
  display: none; 
}

.searchbox-spinner__decrement,
.searchbox-spinner__increment {
  position: absolute;
  top: 50%;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  cursor: pointer;
  border-radius: 50%;
  
  transform: translateY(-50%);

  &:hover {
    background-color: #eee;
  }

  &::selection {
    background-color: transparent;
  }
}

.searchbox-spinner__decrement {
  left: 5px;
}

.searchbox-spinner__increment {
  right: 5px;
}

.searchbox-occupancy-info {

}

.searchbox-occupancy-info__item {
  
  &:not(:last-child):after {
    content: ','
  }
}

.searchbox-occupancy-info__number {
  font-weight: bold;
}

.searchbox-datepicker {

}

.searchbox-datepicker__input {
  display: none;
}